@import '../../styles/variables'; 

.rating{
  border: none;
  display: flex;
  gap: 0;

  &__item{
    margin: 0;
    padding: 0;
    height: 1.2em;
    width: 1.2em;
    font-size: inherit;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; 
    
    color: transparent;
    background: $primaryGradient;
    -webkit-background-clip: text;
    background-clip: text;

    &:focus, &:hover{
      outline: none;
      text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    &:disabled {
      text-shadow: none;
    }
  }
}